import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { StyleRules } from 'material-ui/styles/withStyles';
import { Idispatcher } from './Index';
import { ReceiptAmountState } from './models';
import ListItem from 'material-ui/List/ListItem';
import Avatar from 'material-ui/Avatar/Avatar';
import ListItemText from 'material-ui/List/ListItemText';
import ICON from 'material-ui-icons/MonetizationOn';
import grey from 'material-ui/colors/grey';
import Typography from 'material-ui/Typography/Typography';
import List from 'material-ui/List/List';
import Divider from 'material-ui/Divider/Divider';
import { BasketState } from '../basket/models';
import green from 'material-ui/colors/green';
import pink from 'material-ui/colors/pink';
import Button from 'material-ui/Button/Button';
import { RouteComponentProps, withRouter } from 'react-router';
import dc from '../../constants/DomainConstants';

const styles: StyleRules<  'top' | 'listItem' | 'footer' | 'footerAction'> = {
    top: {
        width: '100%',
    },
    listItem: {
        color: grey[50],
        display: 'flex'
    },
    footer: {
        width: '100%',
        display: 'flex',
        justifyContent: 'flex-end',
    },
    footerAction: {
        marginRight: 10
    }
};

type ClassNames = keyof typeof styles;

interface ReceiptAmountProps {
}

class ReceiptAmountContainer extends React.Component<Idispatcher &
                                            ReceiptAmountProps &
                                            ReceiptAmountState &
                                            BasketState & 
                                            RouteComponentProps<any>&
                                            WithStyles<ClassNames>, 
                                            {}> {
    bill = () => {
        const {ReceiptAmount, TotalPayment} = this.props;
        this.props.billProcess(ReceiptAmount - TotalPayment);
        this.props.history.push(dc.home);
    }

    render(): JSX.Element {
        const {classes, ReceiptAmount, CoinCounters, TotalPayment} = this.props;
        let receiptColor = green[100];
        let disable = false;
        if (ReceiptAmount < TotalPayment) {
            receiptColor = pink[300];
            disable = true;
        }

        return (
        <div className={classes.top}>
            <List>
            {
                CoinCounters.map(ct =>  (
                    <ListItem key={ct.type} dense={true}  className={classes.listItem}>
                      <Avatar alt="Remy Sharp" >
                        <ICON/>
                      </Avatar>
                      <ListItemText 
                        primary={
                        <Typography type="body1" style={{ color: grey[50] }}>{ct.type}えん</Typography>}
                      />
                      <Typography type="body1" style={{ color: grey[50] }}>{ct.count}まい</Typography>
                    </ListItem>
                  ))
            }
            </List>
            <Divider light={true}/>
            <ListItem>
                <ListItemText 
                    primary={
                    <Typography type="headline" style={{ color: grey[50] }}>うけとり</Typography>}
                />
                <Typography type="headline" style={{ color: receiptColor }}>{ReceiptAmount}</Typography>
            </ListItem>
            <ListItem>
                <ListItemText 
                    primary={
                    <Typography type="title" style={{ color: grey[50] }}>ごうけい</Typography>}
                />
                <Typography type="title" style={{ color: grey[50] }}>{TotalPayment}</Typography>
            </ListItem>
            <ListItem>
                <ListItemText 
                    primary={
                    <Typography type="title" style={{ color: grey[50] }}>おつり</Typography>}
                />
                <Typography type="title" style={{ color: grey[50] }}>
                    {disable ? '-' : (ReceiptAmount - TotalPayment) + 'えん'}
                </Typography>
            </ListItem>
            <div className={classes.footer}>
                <div className={classes.footerAction}>
                <Button 
                    raised={true} 
                    color="primary" 
                    onClick={this.bill}
                    disabled={disable}
                >
                    せいさん
                </Button>
                </div>
            </div>
        </div>
        );
    }
}

const StyledLiveContainer = withStyles<{} & ClassNames>(styles)<Idispatcher & 
                            ReceiptAmountState & BasketState & RouteComponentProps<any> 
                            & ReceiptAmountProps>(ReceiptAmountContainer);

export default withRouter(StyledLiveContainer);


