import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { StyleRules } from 'material-ui/styles/withStyles';
import { Idispatcher } from './Index';
import { BasketState } from './models';
import ListItem from 'material-ui/List/ListItem';
import Avatar from 'material-ui/Avatar/Avatar';
import ListItemText from 'material-ui/List/ListItemText';
import List from 'material-ui/List/List';
import IconButton from 'material-ui/IconButton/IconButton';
import UpIcon from 'material-ui-icons/KeyboardArrowUp';
import DownIcon from 'material-ui-icons/KeyboardArrowDown';
import NextIcon from 'material-ui-icons/NavigateNext';
import Button from 'material-ui/Button/Button';
import Divider from 'material-ui/Divider/Divider';
import Typography from 'material-ui/Typography/Typography';
import grey from 'material-ui/colors/grey';
import {  RouteComponentProps, withRouter } from 'react-router';
import dc from '../../constants/DomainConstants';

const styles: StyleRules<  'top' | 'countText' | 'basketFooter' | 'basketActions' > = {
    top: {
        width: '100%',
    },
    countText: {
        width: 50
    },
    basketFooter: {
        marginTop: 10,
        width: '100%',
        display: 'flex',
        justifyContent: 'flex-end',
    },
    basketActions: {
        marginRight: 20,
    }
};

type ClassNames = keyof typeof styles;

interface BasketProps {
}

class BasketContainer extends React.Component<Idispatcher &
                                            BasketProps &
                                            BasketState &
                                            RouteComponentProps<any> &
                                            WithStyles<ClassNames>, 
                                            {}> {
    nextStep = () => {
        this.props.history.push(dc.pay);
    }

    increment = (id: string) => {
        this.props.incrementGoods(id);
    }

    decrement = (id: string) => {
        this.props.decrementGoods(id);
    }

    render(): JSX.Element {
        const { classes, counterList, TotalPayment }  = this.props;
        return (
        <div className={classes.top}>
            <List >
            {counterList.map((tile) => {
                return (
                <ListItem key={tile.id} className={classes.top} >
                    <Avatar alt={tile.displayName} src={tile.img} />
                    <ListItemText 
                        primary={<Typography type="body1" style={{ color: grey[50] }}>
                                            {tile.displayName}
                                 </Typography>} 
                        secondary={<Typography type="body1" style={{ color: grey[50] }}>
                                {tile.price + 'えん'}
                                </Typography>} 
                    />
                    <Typography 
                        type="subheading" 
                        style={{ color: grey[50] }}
                        className={classes.countText}
                    >
                    {tile.count}
                    </Typography>
                    <IconButton onClick={this.increment.bind(this, tile.id)}>
                        <UpIcon color="secondary"/>
                    </IconButton>
                    <IconButton onClick={this.decrement.bind(this, tile.id)}>
                        <DownIcon color="secondary"/>
                    </IconButton>
                </ListItem>
                );
                })
            }
            </List>
            <Divider />
            <div>
            <ListItem>
                <ListItemText 
                    primary={
                    <Typography type="headline" style={{ color: grey[50] }}>ごうけい</Typography>}
                />
                <Typography type="headline" style={{ color: grey[50] }}>{TotalPayment}</Typography>
            </ListItem>
            </div>
            <div className={classes.basketFooter}>
                <div className={classes.basketActions}>
                <Button 
                    raised={true} 
                    color="primary" 
                    disabled={counterList.length === 0}
                    onClick={this.nextStep}
                >
                    おかいけい
                    <NextIcon />
                </Button>
                </div>
            </div>
        </div>
        );
    }
}

const StyledContainer = withRouter(withStyles<{} & ClassNames>(styles)<Idispatcher & 
                            BasketProps & BasketState & RouteComponentProps<any>>(BasketContainer));

export default StyledContainer;


